.stars {
  display: flex;
  &__star {
    width: 16px;
    height: 16px;
    background: url(../images/star.svg) no-repeat;
    background-position: 50%;
    &:not(:last-child) {
      margin-right: 4px;
    }
  }
  @for $starCount from 1 through 5 {
    &--#{$starCount} .stars__star:nth-child(-n + #{$starCount}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
